.auth-container {
  max-width: 420px;
  margin: 80px auto;
  background: #ffffff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.auth-container h1 {
  margin-bottom: 20px;
  color: #003c9d;
  font-size: 28px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.auth-form input {
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
}

.auth-form button {
  background: #003c9d;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 6px;
  font-size: 18px;
  cursor: pointer;
  transition: 0.2s ease;
}

.auth-form button:hover {
  background: #002b70;
}

.auth-message {
  margin-top: 15px;
  font-size: 16px;
  color: green;
}